footer {
  display: flex;
  justify-content: space-between; /* Distribute the items evenly */
  padding: 20px;
  background-color: #ffffff; /* Light background color for the footer */
  border-top: 1px solid #ffffff; /* Optional border for separation */
  margin-left: 20%; /* Adjust as needed */
  margin-right: 20%; /* Add right margin to center the footer */
  background: transparent;


}

.footer-container{
  /* margin-top: 5%; */
  border-top: 1px solid black;
  background: linear-gradient(180deg, #E1BEE7 20%, rgba(196, 243, 198, 1) 100%);


}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none; 
  font-size: 18px;
}

ul li {
  margin-bottom: 10px; /* Space between list items */
}

ul li:first-child {
  font-weight: bold; /* Bold for the section titles */
}

a {
  text-decoration: none; /* Remove underline from links */
  color: #6495ED; /* Preferred link color */
}

a:hover {
  text-decoration: underline; /* Underline on hover */
}



@media (max-width:500px) {
  .footer-container{
    width:100vw;
    height: 40vh;
  }
}
