body{
  overflow-x: hidden;
}

.home-container {
  display: flex;
  flex-direction: row;
  width:100vw;
  height: 85vh;
    /* border: 1px solid black; */
    background: linear-gradient(180deg, #FFFCFF 63%, #E1BEE7 100%);


}

.home-content {
  margin-left: -1%;
  margin-right: auto;
  border: 1px solid black;
  /* overflow-x: hidden; */
  height: 95%;
  width:77%;

}

img {
  /* margin-left: 5%;  */
  margin-top: 20%;
}

h1 {
  font-size: 35px;
  text-align: center;
  /* margin-left: -25%; */
}

/* .step-1{
} */
.allp {
  margin-top: -1%;
  font-size: 20px;
  /* line-height: 2rem; */
  width:95%;
  height: 60%;
  /* border: 1px solid black; */
  text-align: center;
}

.steps {
  /* border: 1px solid black; */
  width:90%;
  height: 60%;
  margin-left: auto;
  margin-top:0%;
  text-align: left;
}

.btn_TakeQuiz{
  margin-left: auto;
  margin-right: auto;
  text-align: center;
}

.btn_TakeQuiz button {
  /* margin-top: 20px; */
  background-color: #000000;
  border: none;
  color: white;
  padding: 16px 32px;
  font-size: 35px;
  transition-duration: 0.4s;
  cursor: pointer;
  border-radius: 10px;

}

.btn_TakeQuiz button:hover {
  background-color: #ffffff;
  color: #000000;
  border: 1px solid black;
}


@media(max-width:500px){
  
  .home-container {
    display: flex;
    flex-direction: row;
    height: 67vh;  
    width: 100vw;
        /* border: 1px solid black; */

  }
      
  .home-content {
    width:70vw;
    height: 70vh;
    align-items: center;
    /* border: 1px solid black; */
    margin-right: auto;
  } 
  
  img {
    width: 120%; /* Adjust the size of the image */
    height: 25%;
    /* border:1px solid black; */
    margin-top:8%;
  }
  
  .home-container h1 {
    
    font-size: 25px;
    text-align: center;
    margin-top: 10%

   
  }
  
  .allp {
    font-size: 15px;

    /* border: 1px solid black; */
    width:150%;
    height:60% ;
    margin-left: -55%;
    margin-top: 15%;
    display: flex;
    flex-direction: column;
  

  }
  
  .steps {
    margin-top: -2%;
    line-height: 135%;
  }
  

  .btn_TakeQuiz:hover {
    background-color: #ffffff;
    color: #000000;
    /* border: 1px solid #000000; */
  }

  .btn_TakeQuiz{
    /* border: 1px solid #000000; */
    width:60%;
    height: 17%;
    margin-right: auto;
    margin-left: auto;
    margin-top: 13%;
  } 

  .btn_TakeQuiz button{
    width:100%;
    height: 100%;
    font-size: 150%;
  }

.last-p{
  margin-top: -20%;
  margin: auto;
  line-height: 130%;
}

.intro-text{
  display: none;
}

}
